import React from 'react';
import { v4 as uuidv4 } from 'uuid';
import Task from './Task.js';

function Content({ dayWidth, days, dateStart, dateEnd, data, isOnMobile, userEmail, userToken, deleteTask }) {

    let elements = [];

    data.forEach((el, i, arr) => {
        el.taskList.forEach((task) => {
            let date = new Date(task.startDate)
            let endDate = new Date(task.endDate)
            console.log(task.startDate)
            console.log(task.endDate)
            console.log(task)
            elements.push(<Task dateStart={dateStart}
                dateEnd={dateEnd}
                name={task.name}
                start={`${date.getFullYear()}-${date.getMonth() < 10 ? "0" + date.getMonth() : date.getMonth()}-${date.getDate() < 10 ? "0" + date.getDate() : date.getDate()}`}
                end={`${endDate.getFullYear()}-${endDate.getMonth() < 10 ? "0" + endDate.getMonth() : endDate.getMonth()}-${endDate.getDate() < 10 ? "0" + endDate.getDate() : endDate.getDate()}`}
                key={uuidv4()}
                days={days}
                dayWidth={dayWidth}
                isOnMobile={isOnMobile}
                projId={el.id}
                taskId={task.id}
                userEmail={userEmail}
                userToken={userToken}
                deleteTask={deleteTask} />)
        })

    }
    )
    console.log(elements)
    return (
        <div id='content'>
            {elements}
        </div>
    )
}

export default Content;
